.background_size{
  background-color: #6ab04c;
  height: 100vh;
  width: 100vw;
}
.container_size{
  width: 1050px;
  height: 350px;
}
.main_container{
  background-color: black;
  width: 340px;
  height: 400px;
}
.button_design button{
  border: black;
  width: 74px;
  height: 80px;
  margin: 2px;
  font-size: 40px;
}
.bg_color{
  background-color: orange;
}
.input_heightwidth{
  width: 340px;
  height: 50px;
  font-size: 25px;
}
.marks_design button{
  width: 60px;
  height: 60px;
  font-size: 30px;
}
.font_sizes{
  font-size: 22px !important;
}
.txt input{
  text-align: right;
}
.box_shadow{
  box-shadow: 10px 10px 8px #000000;
}

@media only screen and (max-width: 768px) {
  .background_size {
    width: 100vw;
    height: 100vh;
  }
    .main_container{
      width: 170px;
      height: 200px;
    }
    .container_size{
      width: 525px;
      height: 175px;
    }
    .button_design button{
      width: 35px;
      height: 38px;
      margin: 1px;
      font-size: 20px;
    }
    .input_heightwidth{
      width: 170px;
      height: 25px;
      font-size: 13px;
    }
    .marks_design button{
      width: 20px;
      height: 20px;
      font-size: 12px !important;
      padding: 0%;
    }
    .font_sizes{
      font-size: 7px !important;
    }
    .marks_design .btn{
      font-size: 15px;
    }
  .sign_position{
    position: relative;
    bottom: 12px;
    right: 5px;
  }
}